function ready(fn) {
    if (document.readyState != 'loading'){
      fn();
    } else {
      document.addEventListener('DOMContentLoaded', fn);
    }
}

function moveImage(image){
    image.style.top = Math.random()*(document.body.scrollHeight - window.innerHeight) + 'px';
    image.style.left = (Math.random()*window.innerWidth ) - (window.innerWidth/4) + 'px';
}

function moveImages(){
    const images = document.querySelectorAll('.image');
    images.forEach(image => moveImage(image));
    images.forEach((image, index) => {
        console.log(index);
        setTimeout(()=>{
            console.log(image);
            setInterval(()=>{
                moveImage(image)
            }, 500)
        }, 1000 * (index+1) )
    });
}
    
function init(){
    moveImages();
}  

ready(init);